import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { BureauComponent } from './bureau.component';
import { BureauListComponent } from './list/bureau-list.component';
import { BureauEditComponent } from './editor/bureau-edit.component';
 import { ProjectConfigResolver } from './interceptors/project.config.resolver';

@NgModule({
  imports: [
      RouterModule.forChild([
        {path: '', component: BureauComponent,
          children: [
            {path: '', component: BureauListComponent},
            {path: ':openType',component: BureauEditComponent},
            {path: ':openType/:code',component: BureauEditComponent }
          ],resolve: {
            envConfiguration: ProjectConfigResolver
          }
      }
    ])
  ],
  exports: [RouterModule]
})
export class BureauRoutingModule { }
