import { Component, OnInit, Input  } from '@angular/core';
import { Bureau } from './models/bureau.model';
import { BureauService } from './services/bureau.service';
import { ToastrService } from 'ngx-toastr';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { TranslateService } from '@ngx-translate/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable, Subscription } from 'rxjs';
import { release } from '../environments/release';

@Component({
  selector: 'lib-root',
  templateUrl: './bureau.component.html',
  styleUrls: ['./bureau.component.scss']
})
export class BureauComponent implements OnInit {

  goBackLink: string;
  openType:string = "";
  resourceId:string = "";
  faTimes = faTimes;

  showLoading: boolean = false;
  arqLoading$: Observable<boolean>;

  loadSubs:Subscription;
  release: string = release.version;

  constructor(private route: ActivatedRoute,
              private router: Router,
              private bureauService: BureauService,
              private toastr: ToastrService,
              public translate:TranslateService
              ) {
                this.arqLoading$ = this.bureauService.loading$;
                this.loadSubs = this.arqLoading$.subscribe((status)=>{
                  this.showLoading = status;
                });
                this.goBackLink = '/external-'+ this.bureauService.getResourceName();
                
              }


  ngOnInit() {
    this.route.params.subscribe(params => {
      console.log(params)
      if (params.hasOwnProperty("openType"))
        this.openType = params.get("openType");
      if (params.hasOwnProperty("code"))
        this.resourceId = params.get("code");
    });
  }
}

 


