import { Component, OnInit, ViewChildren } from '@angular/core';
import { BureauService } from '../services/bureau.service';
import { DecimalPipe } from '@angular/common';
import { faPlus, faPencilAlt, faTrashAlt } from '@fortawesome/free-solid-svg-icons';
import { Observable } from 'rxjs';
import { Bureau } from '../models/bureau.model';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'bureau-list',
  templateUrl: './bureau-list.component.html',
  styleUrls: ['../bureau.component.scss']
})
export class BureauListComponent implements OnInit {
  title = '';
  resourceName = '/';
  faPlus = faPlus;
  faPencilAlt = faPencilAlt;
  faTrashAlt = faTrashAlt;
  bureaus$: Observable<Bureau[]>;
  static LOCALE_KEY = 'currentLocale';
  constructor(private route: ActivatedRoute,
              private router: Router,
              private bureauService: BureauService,
              private toastr: ToastrService,
              public translate: TranslateService) { 
    
    this.bureaus$ = bureauService.items$;
    this.resourceName = '/external-'+ bureauService.getResourceName();
    
    translate.setDefaultLang('ptBR');
    const currentLocale =  sessionStorage.getItem(BureauListComponent.LOCALE_KEY);
    
    translate.setDefaultLang(currentLocale);
  }

  ngOnInit() {
    this.bureauService.loadEmpresas();
    this.bureauService.loadPoliticas();
    this.bureauService.loadBureaus();
  }

  create(): void {
    this.router.navigate([this.resourceName, 'create']);
  }

  change(indexId:string): void {
    this.router.navigate([this.resourceName, 'change', indexId]);
  }

  remove(indexId:string): void {
    this.router.navigate([this.resourceName, 'remove', indexId]);
  }

  clear(): void {
    this.bureauService.loadBureaus();
  }

}
