import { Bureau } from '../models/bureau.model';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';
import { BureauService } from '../services/bureau.service';
import { Subscription } from 'rxjs';
import { faCaretDown, faCaretUp } from '@fortawesome/free-solid-svg-icons';
import { LovVals } from '../models/lovvals.model';

@Component({
  selector: 'bureau-edit',
  templateUrl: './bureau-edit.component.html',
  styleUrls: ['../bureau.component.scss']
})
export class BureauEditComponent implements OnInit, OnDestroy {

  subtitle: string;
  resourceId: string;
  openType: string;

  goBackLink: string;
  content: Bureau;
  originalCopy: Bureau;

  faCaretDown=faCaretDown;
  faCaretUp=faCaretUp;

  errors: string;
  static LOCALE_KEY = 'currentLocale';
  
  lovSELYESNO: LovVals[] = [{ id: "SELYESNO", codigo:"S", descricao:'YES', dadoPadrao:""}, { id: "SELYESNO", codigo:"N", descricao:'NO', dadoPadrao:""}];

  /*contentForm = new FormGroup({
    "cdBureau": new FormControl('', [
      Validators.required,
      Validators.maxLength(3)
    ]),
    "nomeBureau": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ]),
    "descBureau": new FormControl('', [
      Validators.required,
      Validators.maxLength(30)
    ]),
    "bureauAtivo": new FormControl('', [
      Validators.required,
      Validators.maxLength(1)
    ]),
    "bureauExterno": new FormControl('', [
      Validators.required,
      Validators.maxLength(1)
    ]),
    "consultaBureau": new FormControl('', [
      Validators.required,
      Validators.maxLength(1)
    ]),
    "negBureau": new FormControl('', [
      Validators.required,
      Validators.maxLength(1)
    ]),
    "remessa": new FormControl('', [
      Validators.required,
      Validators.maxLength(1)
    ])
  });*/

  private bureauSubs: Subscription;

  constructor(private route: ActivatedRoute,
    private router: Router,
    private bureauService: BureauService,
    private toastr: ToastrService,
    public translate: TranslateService) {

    translate.setDefaultLang('ptBR');
    const currentLocale = sessionStorage.getItem(BureauEditComponent.LOCALE_KEY);

    translate.setDefaultLang(currentLocale);

    this.goBackLink = '/external-' + this.bureauService.getResourceName();
  }

  ngOnInit() {
    this.resourceId = this.route.snapshot.paramMap.get('code');
    this.openType = this.route.snapshot.paramMap.get('openType');
    this.subtitle = 'BUREAU.ITEMSUBTITLE.' + this.openType.toUpperCase();

    if (this.resourceId === null || this.resourceId == undefined)
      this.bureauSubs = this.bureauService.startNewItem()
        .subscribe(item => {
          this.content = item;
          //this.contentForm.patchValue(this.content);
        });
    else
      this.bureauSubs = this.bureauService.selectItem(this.resourceId)
        .subscribe(item => {
          this.content = (item === null) ? this.content : item;
        });

    //this.originalCopy =JSON.parse(JSON.stringify(this.content));
    //console.log(this.content.cdBureau);
    //this.contentForm.patchValue(this.content);
    this.errors = '';
  }

  ngOnDestroy() {
    console.log('Cleaning Bureaus...');
    if (this.bureauSubs != undefined) {
      this.bureauSubs.unsubscribe();
      this.bureauSubs.remove(this.bureauSubs);
    }
  }

  setRemoverTemplate() {

  }

  save() {
    switch (this.openType) {
      case 'remove':
        if ((this.bureauService.validaPolitica(this.content.nomeBureau) === 0) && (this.bureauService.validaEmpresa(this.content.nomeBureau) === 0)) {
          this.bureauService.remove(this.content.cdBureau, () => this.exit());
        }
        else if (this.bureauService.validaPolitica(this.content.nomeBureau) !== 0) {
          let msg = `Este cÃ³digo Bureau possui politicas cadastradas.`;
          this.toastr.error(msg, "Erro:");
        }
        else {
          let msg = `Existem empresas cadastradas com este cÃ³digo Bureau`;
          this.toastr.error(msg, "Erro:");
        }
        break;
      case 'change':
        this.bureauService.save([this.content], () => this.exit());
        break;
      case 'create':
        if (this.bureauService.validaBureau(this.content.cdBureau, this.content.nomeBureau) === 0) {
          this.bureauService.include(this.content, () => this.exit());
        }
        else {
          let msg = `JÃ¡ existe um Bureau cadastrado com este nome ou cÃ³digo`;
          this.toastr.error(msg, "Erro:");
        }
        break;

      default:
        break;
    }
  }
  cancel() {
    this.bureauService.rollbackChanges();
    //this.content = this.originalCopy;
    //let contentId = this.content ? this.content.codigoCriterioRemuneracao : null;
    this.exit();
  }

  exit(): void {
    this.router.navigate([this.goBackLink]);
  }
  get diagnostic() { return JSON.stringify(this.content); }

}
