import { Injectable } from '@angular/core';
import { Observable, of, BehaviorSubject, throwError } from 'rxjs';
import { catchError, finalize, tap } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Bureau } from '../models/bureau.model';
import { HttpErrorHandler, HandleError } from './http-error-handler.service';
import { ToastrService } from 'ngx-toastr';
import { Politica } from '../models/politica.model';
import { EmpresaXBureau } from '../models/empresa-bureau.model';
import { environment } from '../../environments/environment';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

function fullMatch(bureau: any, cdBureau: string) {
  let item: Bureau = bureau as Bureau;
  return (item.cdBureau.toString() === cdBureau);
}

/* FunÃ§Ãµes validaÃ§Ã£o */

function validaEmpresaB(empresa: any, nomeBureau:string){
  let item: EmpresaXBureau = empresa as EmpresaXBureau;
  return item.nomeBureau.includes(nomeBureau); 
} 

function validaBureauB(bureau: any, cdBureau:string,nomeBureau:string){
  let item: Bureau = bureau as Bureau;
  return item.cdBureau.toString().includes(cdBureau) || item.nomeBureau.includes(nomeBureau); 
} 

function validaPoliticaB(politica: any, nomeBureau:string){
  let item: Politica = politica as Politica;
  return item.lstBureau.includes(nomeBureau); 
} 

/* Fim funÃ§Ãµes validaÃ§Ã£o */

@Injectable({
  providedIn: 'root'
})

export class BureauService{
  private _servicePolitica = "Politica";
  private _serviceEmpresa = "EmpresaBureau";
  private _serviceBureau = "Bureau";
  private listaBureaus:Array<Bureau> = [];
  private listaPoliticas:Array<Bureau> = [];
  private listaEmpresas:Array<Bureau>;

  private _loading$ = new BehaviorSubject<boolean>(false);
  private _bureaus$ = new BehaviorSubject<Bureau[]>([]);
  private _politicas$ = new BehaviorSubject<Bureau[]>([]);
  private _empresas$ = new BehaviorSubject<Bureau[]>([]);
  private handleError: HandleError;
  
  private originalCopyBureau: Bureau = null;

  constructor(private http: HttpClient,
              private httpErrorHandler: HttpErrorHandler,
              private toastr: ToastrService) {
    this.handleError = httpErrorHandler.createHandleError(this._serviceBureau);
  } 

  get items$(){return this._bureaus$.asObservable();}
  get politicas$(){return this._politicas$.asObservable();}
  get empresas$(){return this._empresas$.asObservable();} 
  get loading$() { return this._loading$.asObservable(); }
  

  getResourceName():string{
    return 'bureau';
  }

  startNewItem():Observable<Bureau>{
    let selectedItem:Bureau = new Bureau();
    this.originalCopyBureau = null;
    //this._selectedItem$.next(this.selectedItem);
    return of(selectedItem);
  }
  
  selectItem(cdBureau: string):Observable<Bureau>{
    if (cdBureau === null  || cdBureau === undefined) {
      console.log(`${this._serviceBureau}: Bureau selecionado Ã© invÃ¡lido!`);
      let msg = `Bureau escolhido Ã© invÃ¡lido!`;
      this.toastr.error(msg, "CyberMessenger");
      return of(null);
    }
    let selectedItem = this.listaBureaus.find(item => fullMatch(item, cdBureau));
    this.originalCopyBureau =JSON.parse(JSON.stringify(selectedItem));
    return of(selectedItem); 
  }

  rollbackChanges() {
    if (this.originalCopyBureau === null) return;
    let selectedIndex = this.listaBureaus.findIndex(item => fullMatch(item, this.originalCopyBureau.cdBureau.toString()));
    this.listaBureaus[selectedIndex] = this.originalCopyBureau;
    this._bureaus$.next(this.listaBureaus);
  }

  /* ValidaÃ§Ãµes */

  validaPolitica(nomeBureau: string){
    let items = this.listaPoliticas.filter(item => validaPoliticaB(item,nomeBureau));
    return items.length;
  } 

  validaEmpresa(nomeBureau: string){
    let items = this.listaEmpresas.filter(item => validaEmpresaB(item,nomeBureau));
    return items.length;
  }

  validaBureau(cdBureau:number,nomeBureau:string){
    let items = this.listaBureaus.filter(item => validaBureauB(item,cdBureau.toString(),nomeBureau));
    return items.length;
  }

  /* Fim validaÃ§Ãµes */

  loadBureaus(): void {
    console.log(this._serviceBureau + " Lista de bureaus selecionados");
    this.getBureaus().subscribe(
        data => {
          this.listaBureaus = data;
          this._bureaus$.next(this.listaBureaus);
        }
      );
  }

  getBureaus(): Observable<Bureau[]> {
    this._loading$.next(true);
    console.log(this._serviceBureau + " Lista de bureaus selecionados");
    return this.http.get<Bureau[]>(`${environment.serviceUrls.bureausUrl}/listarBureau/`)
    .pipe(
      finalize(() => this._loading$.next(false)),
      tap(data => this.log(`${this._serviceBureau}/loadBureaus`, data)),
      catchError(this.handleError<Bureau[]>('Carregar Bureaus', []))
    );
  }

  loadPoliticas(): void {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._servicePolitica + " Lista de politicas selecionadas");
    this.getPoliticas().subscribe(
        data => {
          this.listaPoliticas = data;
          this._politicas$.next(this.listaPoliticas);
        }
      );
  }

  getPoliticas(): Observable<Bureau[]> {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._servicePolitica + " Lista de politicas selecionadas");
    
    return this.http.get<Bureau[]>(`${environment.serviceUrls.politicaUrl}/listarPoliticaNova/`)
    .pipe(
      tap(data => this.log(`${this._servicePolitica}/loadPoliticas`, data)),
      catchError(this.handleError<Bureau[]>('Carregar Politicas', []))
    );
  }

  loadEmpresas(): void {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceEmpresa + " Lista de empresas selecionadas");
    this.getEmpresas().subscribe(
        data => {
          this.listaEmpresas = data;
          this._empresas$.next(this.listaEmpresas);
        }
      );
  }

  getEmpresas(): Observable<Bureau[]> {
    console.log(" Lista de empresas selecionadas");
    return this.http.get<Bureau[]>(`${environment.serviceUrls.empresaUrl}/listarTodos/`)
    .pipe(
      tap(data => this.log(`${this._serviceEmpresa}/loadEmpresas`, data)),
      catchError(this.handleError<Bureau[]>('Carregar Empresas', []))
    );
  } 

  remove(cdBureau: number, done): void {
    this._loading$.next(true);
    let retVal: Observable<Bureau> = this.http.delete<Bureau>(`${environment.serviceUrls.bureausUrl}/${cdBureau}`, httpOptions);
    
    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(_ => console.log(_)),
      catchError(this.handleError<Bureau>('Remover'))
    ).subscribe(data => {
      if(data['entity']!==null){
        let itemIndex = this.listaBureaus.findIndex((item) => item['cdBureau'] === cdBureau);
        if (itemIndex>=0) {
          this.listaBureaus.splice(itemIndex,1);
          console.log(`${this._serviceBureau}: Bureau [${cdBureau}] removido com sucesso`);
        }
        let msg =  `Bureau removido com sucesso!`; 
        this.toastr.success(msg, "CyberMessenger");
        this._bureaus$.next(this.listaBureaus);
        done();        
      };
    }, error => {
      console.log(`Ocorreu uma falha ao remover o bureau ${cdBureau}!: ` + error.error.message);
      this.toastr.error("Ocorreu uma falha ao remover o bureau!", "CyberMessenger");
    });
  }

  save(items: Bureau[], done): void {
    this._loading$.next(true);
    let retVal: Observable<Bureau[]> = this.http.post<Bureau[]>(environment.serviceUrls.bureausUrl, items, httpOptions);
    
    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(_ => console.log(_)),
      catchError(this.handleError<Bureau[]>('Salvar'))
      ).subscribe(changedList => {
        if(changedList['entity']==null){
          console.log(`${this._serviceBureau}: Ocorreu um erro durante a atualizaÃ§Ã£o do item.`);
        }else{
          console.log(`${this._serviceBureau}: Foram atualizados ${items.length} bureaus com sucesso!`);
          let msg =  `Bureau alterado com sucesso!`; 
          this.toastr.success(msg, "CyberMessenger");
          this._bureaus$.next(this.listaBureaus);
          done();
        }
      }, error => {
        console.log(`Ocorreu uma falha ao salvar critÃ©rios!: ` + error.error.message);
        this.toastr.error("Ocorreu uma falha ao salvar critÃ©rios!", "CyberMessenger");
      });
  }

  include(item: Bureau, done): void {
    this._loading$.next(true);
    let retVal: Observable<Bureau> = this.http.put<Bureau>(`${environment.serviceUrls.bureausUrl}`, item, httpOptions);
    
    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(_ => console.log(_)),
      catchError(this.handleError<Bureau>('Incluir'))
    ).subscribe(data => {
      if(data['entity']===null){
        this.handleError<Bureau>('Incluir');
        console.log(`${this._serviceBureau}: Ocorreu um erro durante a incluisÃ£o do item cÃ³digo=${item['cdBureau']}.`);
      }else{
        console.log(`${this._serviceBureau}: Item cÃ³digo=${data['entity']['cdBureau']} incluido com sucesso!`);
        let msg =  `Bureau ${data['entity']['cdBureau']} salvo com sucesso!`; 
        this.toastr.success(msg, "CyberMessenger");
        
        //this.listaBureaus.push(item);
        this.listaBureaus.unshift(data['entity']);
        this._bureaus$.next(this.listaBureaus);
        done();
      }
    }, error => {
      console.log(`Ocorreu uma falha ao incluir o bureau ${item.cdBureau}!: ` + error.error.message);
      this.toastr.error("Ocorreu uma falha ao incluir bureau!", "CyberMessenger");
    });
  }

  /**
	 * Handle Http operation that succeded.
	 * @param operation - name of the operation that succeded
	 * @param result - Result value to return as the observable
	 */
  private log<Bureau>(operation: string, result: Bureau) {
    // TODO: better job of transforming error for user consumption
    console.log(`${operation} succeded`);

    // Let the app keep running by returning an empty result.
    //return of(result as Bureau);
    return result;
  }

    private logError<Bureau>(operation = 'operation', result?: Bureau) {
      return (error: any): Observable<Bureau> => {
            
       // TODO: send the error to remote logging infrastructure
        console.error(error); // log to console instead
            
       // TODO: better job of transforming error for user consumption
        console.log(`${operation} failed: ${error.message}`);
            
       // Let the app keep running by returning an empty result.
        return of(result as Bureau);
      };
    }
}
